class Index extends React.Component {

  constructor(props) {
    super(props);
    this.handleScroll = this.handleScroll.bind(this);
    this.state = {
      dibawah: false,
      data: '',
    };
  }

  componentDidMount = () => {
    window.addEventListener('scroll', this.handleScroll);
    this.props.dispatch(loadMygram());
  }

  componentWillMount() {
    if (this.props.dataMygram) {
      this.setState({ data: this.props.dataMygram.data });
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.dataMygram && nextProps.dataMygram.data !== this.state.data) {
      this.setState({ data: nextProps.dataMygram.data });
    }
  }
  
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll() {
    let scrOfY = 0;
    let dibawah = false;
    if (typeof (window.pageYOffset) === 'number') {
      // Netscape compliant
      scrOfY = window.pageYOffset;
      dibawah = window.pageYOffset > 50;
      
    } else if (document.body && document.body.scrollTop) {
      // DOM compliant
      scrOfY = document.body.scrollTop;
      dibawah = document.body.scrollTop > 50;
    }
    // return scrOfY;
    return this.setState({ dibawah });
  }

  render() {
    const { classes, dataMygram } = this.props;
    const { dibawah, data } = this.state;
    if (dataMygram === null) {
      this.props.dispatch(loadMygram());
    }
    console.log('jylo', data);
    return (
      <Fragment>
        <Helmet defaultTitle="adnanfajlur">
          <title>adnanfajlur</title>
          <html lang="en" />
          <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Roboto:200,300,400,500" />
        </Helmet>
        <div className={classes.root}>
          <div className={classes.head} style={{ height: dibawah ? 52 : 77 }}>
            <div className={classes.headContent}>
              <MyGramIcon style={{ width: 30, height: 30 }} />
              <div className={classes.searchWrap}>
                <input
                  placeholder="Search"
                  className={classes.searchInput} />
              </div>
            </div>
          </div>
          <div className={classes.body}>
            <div className={classes.bodyHead}>
              <div className={classes.bodyHeadFoto}>
                <img alt="" src={data && data.user.profile_pic_url_hd} className={classes.fotoProfile} />
              </div>
              <div className={classes.bodyHeadDetail}>
                <div className={classes.bodyHeadUser}>
                  <Typography className={classes.typoUserName}>{data && data.user.username}</Typography>
                  <button className={classes.buttonFollow}>Follow
                  </button>
                </div>
                <ul className={classes.ulInfo1}>
                  <li className={classes.liInfo}>
                    <Typography><span style={{ fontWeight: '500' }}>{data && data.user.media.count}</span> posts</Typography>
                  </li>
                  <li className={classes.liInfo}>
                    <Typography><span style={{ fontWeight: '500' }}>{data && data.user.followed_by.count}</span> followers</Typography>
                  </li>
                  <li className={classes.liInfo}>
                    <Typography><span style={{ fontWeight: '500' }}>{data && data.user.follows.count}</span> following</Typography>
                  </li>
                </ul>
                <Typography className={classes.typoFullName1}>
                  {data && data.user.full_name}
                </Typography>
              </div>
            </div>
            <Typography className={classes.typoFullName2}>
              {data && data.user.full_name}
            </Typography>
            <ul className={classes.ulInfo2}>
              <li className={classes.liInfo}>
                <Typography style={{ color: '#999' }}>
                  <span className={classes.typoSpan}>{data && data.user.media.count}</span> posts
                </Typography>
              </li>
              <li className={classes.liInfo}>
                <Typography style={{ color: '#999' }}>
                  <span className={classes.typoSpan}>{data && data.user.followed_by.count}</span> followers
                </Typography>
              </li>
              <li className={classes.liInfo}>
                <Typography style={{ color: '#999' }}>
                  <span className={classes.typoSpan}>{data && data.user.follows.count}</span> following
                </Typography>
              </li>
            </ul>
            <div className={classes.bodyContent}>
              <div className={classes.bodyGrid}>
                {data && data.user.media.nodes.map((n, i) => (
                  <div className={classes.divImage} key={i}>
                    <img alt="" className={classes.responsiveImage} src={n.thumbnail_src} />
                    <div>
                      tes
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  dataMygram: selectMygramData(),
});

Index.propTypes = {
  classes: PropTypes.object.isRequired,
  dispatch: PropTypes.func,
  dataMygram: PropTypes.object,
};

export default connect(mapStateToProps)(withRoot(withStyles(styles)(Index)));



